.page-header {
    padding: 8rem 0 2rem; /* $header-height = 8rem */
    text-align: center;
    margin-bottom: 2rem;
    position: relative;
    background-position: top;
    background-size: cover;
    background-repeat: no-repeat;
}

.page-header h2 {
    position: relative;
    z-index: 99;
}

.page-header::after {
    content: "";
    position: absolute;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100px;
    background-image: linear-gradient(to top, #0f0f0f, rgba(0, 0, 0, 0)); /* $body-bg = #0f0f0f, $black = #000 */
}
